import layzr from 'layzr.js';

export default class LazyLoadClass {

  constructor() {
    this.instance = null;
  }

  /**
   * Init lazy load
   *
   * @param options
   * @returns {*}
   */
  init(options = {}) {
    // checking window object to avoid server rendering error
    if (typeof window !== 'undefined' && this.instance === null) {
      this.instance = layzr(Object.assign({
        normal: 'data-lazy-src',
        retina: 'data-lazy-retina-src',
        srcset: 'data-lazy-srcset',
        threshold: 0,
      }, options));

      this.onAfter((element) => {
        element.classList.add('lazy-image--show');
      });

      this
        .update()           // track initial elements
        .check()            // check initial elements
        .handlers(true);    // bind scroll and resize handlers
    }
  }

  /**
   * Add or remove the scroll and resize event handlers.
   */
  handlers(active) {
    if (this.instance) {
      this.instance.handlers(active);
    }
    return this;
  }

  /**
   * Manually check if elements are in the viewport
   */
  check() {
    if (this.instance) {
      this.instance.check();
    }
    return this;
  }

  /**
   * Dynamically added elements should be handled using this method
   */
  update() {
    if (this.instance) {
      this.instance.update();
    }
    return this;
  }

  /**
   * This event is emitted immediately before an image source is set.
   * The image node is passed to the event handler.
   */
  onBefore(callback) {
    if (this.instance) {
      this.instance.on('src:before', callback);
    }
    return this;
  }

  /**
   * This event is emitted immediately after an image source is set.
   * The image node is passed to the event handler.
   */
  onAfter(callback) {
    if (this.instance) {
      this.instance.on('src:after', callback);
    }
    return this;
  }
}



